import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./ticket-type-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class TicketTypeAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ToastComponent) toast: ToastComponent;
 
ticketTypeForm: FormGroup;
tickettypeId = 0;  
ajaxData : TicketTypeData[];
tickettype= new TicketTypeData();	

status;

msg = [];
bootstrapFromValues = [];

constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.tickettypeId).subscribe(params=>{
			if(params.tickettypeId != undefined){
				this.tickettypeId=params.tickettypeId; 
			}
		}); 
	console.log("tickettypeId=="+this.tickettypeId);
	if(this.tickettypeId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.ticketTypeForm = new FormGroup({
			'tickettypeId' : new FormControl(''),
			'tickettypeName' : new FormControl('', [ Validators.required]),
			'tickettypeEntryId' : new FormControl(''),
			'tickettypeEntryDate' : new FormControl(''),
			'tickettypeModifiedId' : new FormControl(''),
			'tickettypeModifiedDate' : new FormControl(''),
		});
	}
	  
	   	get tickettypeName(){ return this.ticketTypeForm.get('tickettypeName')};
	    get tickettypeEntryId(){ return this.ticketTypeForm.get('tickettypeEntryId')};
	    get tickettypeEntryDate(){ return this.ticketTypeForm.get('tickettypeEntryDate')};
	    get tickettypeModifiedId(){ return this.ticketTypeForm.get('tickettypeModifiedId')};
	    get tickettypeModifiedDate(){ return this.ticketTypeForm.get('tickettypeModifiedDate')};
	 
	 populateData(){
			  	const headers = new HttpHeaders({'Content-Type':'application/json'});
			    this.http.post<TicketTypeData>(sessionStorage.getItem('requestUrl')+'/axelacrm/helpdesk/helpdesktype-data ',
							 			JSON.stringify({ 'tickettypeId' :this.tickettypeId}),
									{ headers, withCredentials: true }).subscribe(data =>{
				  														this.tickettype = data['populateData'][0];
			  															},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['helpdesk/ticket-type-list']);
		 }

 
	  	onSubmit() {
			this.bootstrapFromValues = $('#m_form_1').serializeArray();
			for(var i=0;i<this.bootstrapFromValues.length;i++){
				this.tickettype[this.bootstrapFromValues[i].name]=this.bootstrapFromValues[i].value;
			}
			if(!this.ticketTypeForm.invalid){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.tickettypeId == 0){ 
				this.tickettype['requestType'] = 'add';
			}else{
				this.tickettype['requestType'] = 'update';
				this.tickettype['tickettypeId'] = this.tickettypeId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/tickettype',
									JSON.stringify(this.tickettype),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
				  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.ticketTypeForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
		  } 
	  }
 
  onDelete(){
		 this.tickettype['requestType'] = 'delete';
		this.tickettype['tickettypeId'] = this.tickettypeId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/helpdesk/helpdesktype',
									JSON.stringify(this.tickettype),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.ticketTypeForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}

	
export class  TicketTypeData {
    tickettypeId: string;
    tickettypeName: string;
	tickettypeEntryBy: string;
	tickettypeEntryId: string;
	tickettypeEntryDate: string;
	tickettypeModifiedId: string;
	tickettypeModifiedBy: string;
	tickettypeModifiedDate: string;
  
}


